import { createContext, useState, ReactNode, useCallback, useContext } from 'react'
import { v4 as uuidv4 } from 'uuid'
import { ColorWithId, HSLColor, HarmonyType, RGBColor } from '../types/color'
import { Palette, PaletteGenerationOptions, PaletteAdjustmentOptions } from '../types/palette'
import { UIColorRoleAssignments } from '../types/uiRoles'
import { generatePaletteFromSeed } from '../algorithms/generation/seedBased'
import { generatePaletteFromSemantics } from '../algorithms/generation/semantic'
import { hslToRGB, rgbToHSL } from '../algorithms/colorModels/conversions'
import { rgbToHex } from '../algorithms/colorModels/rgb'
import { rgbToLAB } from '../algorithms/colorModels/conversions'
import { HistoryContext } from './HistoryContext'

interface PaletteContextType {
  currentPalette: Palette
  generateNewPalette: (options: PaletteGenerationOptions) => void
  regeneratePalette: (options: Partial<PaletteGenerationOptions>) => void
  updateColor: (colorId: string, color: Partial<ColorWithId>) => void
  lockColor: (colorId: string, locked: boolean) => void
  updateRoleAssignments: (assignments: UIColorRoleAssignments) => void
  roleAssignments: UIColorRoleAssignments
  reorderColors: (startIndex: number, endIndex: number) => void
  // Add missing methods
  updatePalette: (palette: Palette) => void
  setCurrentPalette: (palette: Palette) => void
}

export const PaletteContext = createContext<PaletteContextType>({
  currentPalette: {
    id: '',
    name: '',
    colors: [],
    harmony: 'analogous',
    timestamp: 0
  },
  generateNewPalette: () => {},
  regeneratePalette: () => {},
  updateColor: () => {},
  lockColor: () => {},
  updateRoleAssignments: () => {},
  roleAssignments: {},
  reorderColors: () => {},
  updatePalette: () => {},
  setCurrentPalette: () => {}
})

interface PaletteProviderProps {
  children: ReactNode
}

export const PaletteProvider = ({ children }: PaletteProviderProps) => {
  const { addToHistory } = useContext(HistoryContext)
  
  const [currentPalette, setCurrentPalette] = useState<Palette>({
    id: uuidv4(),
    name: 'New Palette',
    colors: [],
    harmony: 'analogous',
    timestamp: Date.now()
  })
  
  const [roleAssignments, setRoleAssignments] = useState<UIColorRoleAssignments>({})
  
  const createColorWithId = (hsl: HSLColor): ColorWithId => {
    const rgb = hslToRGB(hsl)
    return {
      id: uuidv4(),
      rgb,
      hex: rgbToHex(rgb),
      hsl,
      lab: rgbToLAB(rgb),
      locked: false
    }
  }
  
  const generateNewPalette = useCallback((options: PaletteGenerationOptions) => {
    const { paletteSize, harmonyType, semanticTerms } = options
    
    // Generate HSL colors based on the selected options
    let hslColors: HSLColor[] = []
    
    if (semanticTerms && semanticTerms.length > 0) {
      // Generate colors based on semantic terms
      hslColors = generatePaletteFromSemantics(semanticTerms, paletteSize)
    } else {
      // Generate a random seed color if no semantic terms
      const randomHue = Math.floor(Math.random() * 360)
      const seedColor: HSLColor = [randomHue, 70, 50]
      hslColors = generatePaletteFromSeed(seedColor, paletteSize, harmonyType)
    }
    
    // Convert HSL colors to ColorWithId objects
    const colors = hslColors.map(createColorWithId)
    
    // Create new palette
    const newPalette: Palette = {
      id: uuidv4(),
      name: 'New Palette',
      colors,
      harmony: harmonyType,
      timestamp: Date.now()
    }
    
    // Add current palette to history before updating
    if (currentPalette.colors.length > 0) {
      addToHistory(currentPalette)
    }
    
    setCurrentPalette(newPalette)
    
    // Reset role assignments when creating new palette
    setRoleAssignments({})
    
  }, [currentPalette, addToHistory])
  
  const regeneratePalette = useCallback((options: Partial<PaletteGenerationOptions>) => {
    const { preserveLocked = true, harmonyType } = options
    
    if (currentPalette.colors.length === 0) {
      generateNewPalette({
        paletteSize: 5,
        harmonyType: harmonyType || 'analogous',
        preserveLocked: false,
        mode: 'light'
      })
      return
    }
    
    // Add current palette to history before updating
    addToHistory(currentPalette)
    
    // Find unLocked colors and their indices
    const lockedColors = currentPalette.colors.filter(color => color.locked)
    const lockedIndices = currentPalette.colors
      .map((color, index) => color.locked ? index : -1)
      .filter(index => index !== -1)
    
    // Find a seed color (first unlocked color or first color if all are locked)
    let seedColor: HSLColor
    const unlockedColor = currentPalette.colors.find(c => !c.locked)
    
    if (unlockedColor) {
      seedColor = unlockedColor.hsl
    } else if (currentPalette.colors.length > 0) {
      seedColor = currentPalette.colors[0].hsl
    } else {
      // Fallback to random hue
      seedColor = [Math.floor(Math.random() * 360), 70, 50]
    }
    
    // Generate new HSL colors
    const newHarmony = harmonyType || currentPalette.harmony
    const hslColors = generatePaletteFromSeed(
      seedColor, 
      currentPalette.colors.length, 
      newHarmony
    )
    
    // Replace unlocked colors with new generated ones
    let newColors: ColorWithId[]
    
    if (preserveLocked && lockedColors.length > 0) {
      newColors = [...currentPalette.colors]
      
      currentPalette.colors.forEach((color, index) => {
        if (!color.locked) {
          const hslIndex = index - lockedIndices.filter(i => i < index).length
          const newHsl = hslColors[hslIndex >= 0 && hslIndex < hslColors.length ? hslIndex : 0]
          const rgb = hslToRGB(newHsl)
          
          newColors[index] = {
            ...color,
            rgb,
            hex: rgbToHex(rgb),
            hsl: newHsl,
            lab: rgbToLAB(rgb)
          }
        }
      })
    } else {
      // Don't preserve locks - regenerate all colors
      newColors = hslColors.map(createColorWithId)
    }
    
    setCurrentPalette({
      ...currentPalette,
      colors: newColors,
      harmony: newHarmony,
      timestamp: Date.now()
    })
  }, [currentPalette, generateNewPalette, addToHistory])
  
  const updateColor = useCallback((colorId: string, updates: Partial<ColorWithId>) => {
    setCurrentPalette(prev => {
      const updatedColors = prev.colors.map(color => {
        if (color.id === colorId) {
          return { ...color, ...updates }
        }
        return color
      })
      
      return {
        ...prev,
        colors: updatedColors,
        timestamp: Date.now()
      }
    })
  }, [])
  
  const lockColor = useCallback((colorId: string, locked: boolean) => {
    updateColor(colorId, { locked })
  }, [updateColor])
  
  const updateRoleAssignments = useCallback((assignments: UIColorRoleAssignments) => {
    setRoleAssignments(assignments)
  }, [])
  
  const reorderColors = useCallback((startIndex: number, endIndex: number) => {
    setCurrentPalette(prev => {
      const result = [...prev.colors]
      const [removed] = result.splice(startIndex, 1)
      result.splice(endIndex, 0, removed)
      
      return {
        ...prev,
        colors: result,
        timestamp: Date.now()
      }
    })
  }, [])
  
  const updatePalette = useCallback((palette: Palette) => {
    setCurrentPalette(palette)
  }, [])

  return (
    <PaletteContext.Provider 
      value={{
        currentPalette,
        generateNewPalette,
        regeneratePalette,
        updateColor,
        lockColor,
        updateRoleAssignments,
        roleAssignments,
        reorderColors,
        updatePalette,
        setCurrentPalette
      }}
    >
      {children}
    </PaletteContext.Provider>
  )
}
