import { useState } from 'react';
import { HSLColor, HarmonyType } from '../types/color';
import { hslToRGB } from '../algorithms/colorModels/conversions';
import { rgbToHex } from '../algorithms/colorModels/rgb';
import { generatePaletteFromSeed } from '../algorithms/generation/seedBased';
import './GeneratePage.css';

const GeneratePage = () => {
  const [palette, setPalette] = useState<Array<{hsl: HSLColor, hex: string}>>([]);
  const [paletteSize, setPaletteSize] = useState(5);
  const [harmonyType, setHarmonyType] = useState<HarmonyType>('analogous');

  const generatePalette = () => {
    // Generate a random seed color
    const randomHue = Math.floor(Math.random() * 360);
    const seedColor: HSLColor = [randomHue, 70, 50];
    
    // Generate palette using algorithm
    const hslColors = generatePaletteFromSeed(seedColor, paletteSize, harmonyType);
    
    // Convert to hex and store
    const newPalette = hslColors.map(hsl => {
      const rgb = hslToRGB(hsl);
      return {
        hsl,
        hex: rgbToHex(rgb)
      };
    });
    
    setPalette(newPalette);
  };

  return (
    <div className="generate-page">
      <h1>Ultimate Color Generator</h1>
      
      <div className="controls">
        <label>
          Palette Size:
          <select 
            value={paletteSize} 
            onChange={(e) => setPaletteSize(parseInt(e.target.value))}
          >
            <option value="3">3</option>
            <option value="5">5</option>
            <option value="7">7</option>
          </select>
        </label>
        
        <label>
          Harmony:
          <select 
            value={harmonyType} 
            onChange={(e) => setHarmonyType(e.target.value as HarmonyType)}
          >
            <option value="analogous">Analogous</option>
            <option value="complementary">Complementary</option>
            <option value="triadic">Triadic</option>
            <option value="tetradic">Tetradic</option>
            <option value="splitComplementary">Split Complementary</option>
            <option value="monochromatic">Monochromatic</option>
          </select>
        </label>
        
        <button className="generate-button" onClick={generatePalette}>
          Generate Palette
        </button>
      </div>
      
      {palette.length > 0 && (
        <div className="palette-display">
          {palette.map((color, index) => (
            <div 
              key={index} 
              className="color-swatch"
              style={{backgroundColor: color.hex}}
            >
              <span className="color-info">{color.hex}</span>
              <span className="color-info">
                HSL: {Math.round(color.hsl[0])}¬∞, {Math.round(color.hsl[1])}%, {Math.round(color.hsl[2])}%
              </span>
            </div>
          ))}
        </div>
      )}
      
      {palette.length === 0 && (
        <div className="placeholder">
          <p>Click "Generate Palette" to create your first color palette!</p>
        </div>
      )}
    </div>
  );
};

export default GeneratePage;
